object {outline:none;}
mark {background: #ff0; color: #000;}
code, kbd, pre, samp {font-family:inherit; font-size: 1em;}
pre {white-space: pre-wrap;}
q {quotes: "\201C" "\201D" "\2018" "\2019";}
small {font-size: 80%;}
sub,
sup {font-size: 75%; line-height: 0; position: relative; vertical-align: baseline;}
sup {top: -0.5em;}
sub {bottom: -0.25em;}
svg:not(:root) {overflow: hidden;}
figure {margin: 0;}
fieldset {padding: 0; border: 0; margin: 0;}
legend {border: 0; padding: 0;}
/* Form Controls */
button, input[type=text],input[type=password], select, textarea {border-radius:0; vertical-align:middle; }
textarea {border:0 none; resize: none;}
input:-moz-placeholder,textarea:-moz-placeholder {color: #ccc !important; font-weight: normal;}
input::-moz-placeholder,textarea::-moz-placeholder {color: #ccc !important; font-weight: normal;}
input:-ms-input-placeholder,textarea:-ms-input-placeholder {color: #ccc !important; font-weight: normal;}
